@import url(https://fonts.googleapis.com/css?family=Exo|Neucha|Overlock&display=swap);
* {
	box-sizing: border-box;
}


body {
	background-color: #333;
    background-image: url(https://res.cloudinary.com/drpcjt13x/image/upload/v1579002769/Proyectos/Random%20Quote%20Machine/iStock-939184094_aq2vlv.jpg);
    background-repeat: no-repeat;
	background-size: cover;
    display: flex;
	justify-content: center;
    align-items: center;
    align-content: center;
    height: 100vh;
    margin: 0;
    padding: 0;
}

p {
    font-family: 'Overlock', cursive;
    font-size: 1.20em;
    margin: 10px 0px;
    text-align: center;
}
#text {
    font-family: 'Neucha', cursive;
    font-size: 1.95em;
    font-weight: bold;
    text-align: center;
    font-style: italic;
    padding: 0px;
    margin: 0px;
}

#author {
    margin-top: 5px;
    font-size: 1.2em;
    text-align: right;
}
.fa-twitter-square{
    font-size: 40px;
}

#tweet-quote:hover {
    opacity: 0.7;
}
#new-quote {       
    height:38px;
    border:none;
    border-radius:3px;
    color:#ffffff;
    font-size:0.85em;
    padding: 6px 18px;
    cursor:pointer;
}

#new-quote:hover {
    opacity: 0.7;
}
#container {
    display: flex;
    flex-direction: column;
    justify-content: center; 
    padding: 20px;
    width: 100%;
    border-radius: 5px;
}

#quote-box {
    padding: 20px 40px;
    background-color: #fff;
    border-radius: 5px;
}

.container-buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 30px;
}
.footer {
    text-align: center;
    font-family: 'Overlock', cursive;
    color:#000000;
    margin-top: 10px;
    font-size: 12px;	
}

a {
    text-decoration: none;
    color: #000000;
}

a:hover {
    color: #333;
}
h1 {
    font-family: 'Overlock', cursive;
    text-align: center;
    margin-bottom: 30px;
    margin-top: 0;
}


